.diaryContainer {
  display: flex;
  flex-direction: column;
  padding-top: 9%;
  min-height: 100vh;
} 

.calendarSection,
.foodListSection,
.summarySection {
  width: 100%;
  max-width: 600px;
  margin-bottom: 20px;
}



.foodListSection {
  background-color: #fff;
  /* padding: 15px; */
  border-radius: 8px;
  overflow-y: auto;
  height: 100vh;
}

.summarySection {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 10px;
  background-color: #fff;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  border-radius: 8px;
}


@media screen and (min-width: 768px) {
  .diaryContainer {
    padding-top: 40px;
    min-height: 93vh;
  }
  .foodListSection {
    padding-left: 15px;
  }
}
.foodListSection {
  height: 69vh;
}

@media (min-width: 1024px) {
  .foodListSection {
    padding-left: 15px;
    padding-right: 40px
  }
  .diaryContainer {
    padding-top: 50px;
    
  }
}